<script>
import homeProject from '@/components/Home/homeProject';
import homeIssue from '@/components/Home/homeIssue';
import Earth from '@/components/Home/earth';
import GlobalSwiper from '@/components/common/GlobalSwiper';
import { getHomeData } from '@/tools/request';
import BackgroundMotion from '@/components/common/BackgroundMotion';

import { mapMutations } from 'vuex';

import Swiper from 'swiper';
import 'swiper/css/swiper.css';

export default {
  name: 'Home',
  metaInfo: {
    title: '', // set a title
    meta: [
      {
        // set meta
        name: 'description',
        content:
          '2021-',
      },
      { name: 'keywords', content: '  ' },
    ],
    // link: [{                 // set link
    //   rel: 'asstes',
    //   href: 'https://assets-cdn.github.com/'
    // }]
  },
  data() {
    return {
      homeData: {},
      isTrue: true, //  
    };
  },
  created() {
    this.getData();
    new this.$wow.WOW({
      live: false,
    }).init();
    window.addEventListener('scroll', this.handleScroll, true);
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.handleScroll, true);
  },
  updated() {
    this.$nextTick(() => {
      if (this.isTrue) {
        this.initSwiper();
        this.isTrue = false;
      }
    });
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        document.body.scrollTop = this.$store.state.srcollNum;
        document.documentElement.scrollTop = this.$store.state.srcollNum;
        window.scrollTo(0, this.$store.state.srcollNum);
      }, 800);
    });
  },
  components: {
    GlobalSwiper,
    homeProject,
    Earth,
    homeIssue,
    BackgroundMotion,
  },
  methods: {
    ...mapMutations(['changeScroll']),
    async getData() {
      let res = await getHomeData();
      this.homeData = res.result;
    },
    initSwiper() {
      new Swiper('.home-swiper', {
        // 
        loop: false,
        // 
        autoplay: false,
        observer: true, // swiperswiper
        observeParents: true, // swiperswiper
        grabCursor: true,
        // slidesPerView: "auto",

        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        // 
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      });
    },
    // 
    handleScroll() {
      this.changeScroll(window.pageYOffset);
    },
  },
};
</script>
